.today-date-time-center {
  min-height: 30vh;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 0;
  color: #333;
  font-weight: bold;
}
.today-date-time-center p {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  font-size: 1.7rem;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
}
.time-container {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}
.time-container span {
  line-height: 1;
  padding: 0.25rem;
  font-size: 1.4rem;
}
@media screen and (max-width: 479px) {
  .today-date-time-center {
    min-height: 5vh;
    flex-shrink: 0;
    /* flex: 0 0 5vh; */
    flex-direction: row;
    padding: 0.5rem 0;
    margin: 0.75rem 0.5rem 0;
  }
  .today-date-time-center p {
    margin: 0;
    height: 100%;
    font-size: 1.5rem;
  }
  .time-container span {
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .today-date-time-center {
    /* flex: 0 0 5vh; */
    min-height: 5vh;
    flex-shrink: 0;
    flex-direction: row;
    padding: 0.5rem 0;
    margin: 0.75rem 0.5rem 0;
  }
  .today-date-time-center p {
    margin: 0;
    height: 100%;
    font-size: 1.6rem;
  }
  .time-container span {
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 768px) {
  .today-date-time-center p {
    padding: 0 0.25rem;
    font-size: 1.7rem;
  }

  .time-container span {
    font-size: 1.4rem;
  }
}

@media screen and (min-width: 1024px) {
  .today-date-time-center p {
    padding: 0 0.25rem;
    font-size: 1.8rem;
  }

  .time-container span {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 1280px) {
  .today-date-time-center p {
    padding: 0 0.25rem;
    font-size: 1.9rem;
  }

  .time-container span {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1440px) {
  .today-date-time-center p {
    padding: 0 0.25rem;
    font-size: 2rem;
  }

  .time-container span {
    font-size: 1.7rem;
  }
}
