.home-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #e0dfd6;
  overflow: hidden;
}

.home-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0.5rem 1rem;
  overflow-y: auto;
  /* border: 1px solid blue; */
}
.home-title {
  min-height: 30vh;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}
.home-title h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem;
  font-size: 2rem;
}
.preview-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  margin: 0.5rem 0;
  padding: 0;
}
.preview-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 0.25rem;
  color: #6d6a5e;
  background-color: #f0f0f0;
  border-radius: 0.25rem;
  /* border: 1px solid red; */
}
.preview-title {
  display: flex;
  flex-direction: column;
  padding: 0.15rem;
  background-color: #f0f0f0;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  /* border: 1px solid blue; */
}
.preview-title h4 {
  font-size: 1.3rem;
  padding: 0.25rem;
}
.todo.preview-title {
}
.agneda-preview-title {
}
.preview-title-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.agenda-current-date {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.25rem;
  font-size: 0.9rem;
  font-weight: bold;
  background-color: #f0f0f0;
  line-height: 1;
}
.preview-summary {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  line-height: 1;
  padding: 0.25rem;
}
.preview-summary span {
  margin: 0 0.5rem;
}

/*previewAgenda, previewList*/
.preview-center {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.preview-todo-list,
.preview-agenda {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  /* grid-auto-rows: 120px; */
  grid-auto-rows: 110px;
  gap: 0.5rem;
  padding: 0.25rem;
  margin: 0 0.25rem;
  word-break: break-word;
  background-color: #f2f1eb;
  overflow-y: auto;
}
.preview-todo-list li,
.preview-agenda li {
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0.75rem;
  /* padding: 0.5rem 0.5rem; */
  font-size: 1.2rem;
  background-color: #e8e3da;
  border-radius: 0.25rem;
  color: #6d6a5e;
  box-shadow: 1px 2px 0 rgba(0, 0, 0, 0.3);
  overflow-y: auto;
  /* border: 1px solid blue; */
}
.agenda-mini-date {
  padding-left: 0.25rem;
  /* margin-bottom: 0.25rem; */
}
.todo-mini-contents,
.agenda-mini-contents {
  padding: 0.25rem;
  /* border: 1px solid red; */
}

/**/
.preview-more,
.preview-add {
  display: flex;
  align-items: center;
  padding-left: 0.25rem;
  background-color: transparent;
  border-color: transparent;
  font-size: 1.1rem;
  font-weight: bold;
  color: #5b5f52;
  background-color: #f0f0f0;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  cursor: pointer;
}
.preview-more:hover,
.preview-add:hover {
  color: #3e3b36;
}
.preview-empty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 40vh;
  font-weight: bold;
  padding: 1rem 0;
}
.preview-empty p {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
  padding: 0.5rem 0.25rem;
  font-size: 1.2rem;
}

.preview-agenda-empty {
  flex-direction: column;
}
.preview-agenda-empty p {
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 479px) {
