    margin: 0.25rem;
    margin: 0;
    font-size: 1.5rem;
  }
  .preview-wrapper {
    flex-direction: column;
    margin: 0.25rem 0;
  }

  .preview-title {
    padding: 0.15rem 0.25rem;
    /* border: 1px solid red; */
  }
  .preview-title h4 {
    font-size: 1.1rem;
    margin: 0;
    padding: 0.25rem 0;
    padding-left: 0.25rem;
    /* border: 1px solid red; */
  }
  .agenda-current-date {
    font-size: 0.8rem;
  }
  .preview-summary {
    margin: 0;
    padding: 0.25rem;
    font-size: 0.85rem;
  }
  .preview-summary span {
    margin: 0 0.25rem;
  }

  .preview-todo-list,
  .preview-agenda {
    max-height: 30vh;
    grid-template-columns: 1fr;
    grid-auto-rows: 100px;
    gap: 0.5rem;
    padding: 0.25rem;
    margin: 0 0.25rem;
  }
  .preview-todo-list li,
  .preview-agenda li {
    /* padding: 0.5rem 0.75rem; */
    padding: 0.5rem;
    font-size: 1rem;
  }
  .agenda-mini-date {
    padding-left: 0.25rem;
  }
  .todo-mini-contents,
  .agenda-mini-contents {
    padding: 0.25rem;
  }
  /**/
  .preview-more,
  .preview-add {
    font-size: 1rem;
  }
  .preview-empty {
    min-height: 30vh;
    font-weight: bold;
    padding: 1rem 0;
    /* border: 1px solid red; */
  }
  .preview-empty p {
    margin: 2rem 0;
    padding: 0.5rem 0.25rem;
    font-size: 1rem;
    /* border: 1px solid blue; */
  }
  .preview-agenda-empty p {
    margin: 0;
    padding: 0.15rem 0.25rem;
  }
}

